<template>
  <div>
    <div style="display: flex;justify-content: space-between;height: 40px;">
      <div>审核通过列表</div>
    </div>
    <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="tableData"
      tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
      :header-cell-style="headerStyle">
      <el-table-column prop="index" label="序号" width="120">
      </el-table-column>
      <el-table-column v-for="(item, index) in tableMsg" :key="index" :prop="item.key" :label="item.name">
      </el-table-column>
      <el-table-column fixed="right" label="操作" width="400">
        <template slot-scope="scope">
          <el-button @click="functionMsg.dialogVisible = true" type="text" size="small">功能开通</el-button>
          <el-button @click="serviceChargeMsg.dialogVisible = true" type="text" size="small">服务费管理</el-button>
          <el-button @click="merchantInformation.dialogVisible = true" type="text" size="small">商户信息</el-button>
          <el-button @click="franchiseOutlets.dialogVisible = true" type="text" size="small">加盟网点查看</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--page start 分页  current-page:当前页，handleSizeChange：每页显示条数改变的时候，page-size：每页显示条数，total：总条数，handleCurrentChange：下一页-->
    <el-pagination style="margin-top: 10px;" background @size-change="handleSizeChange"
      @current-change="handleCurrentChange" :current-page="1" :page-sizes="[10, 20, 50, 100,200]" :page-size="10"
      layout="total, sizes, prev, pager, next, jumper" :total="10">
    </el-pagination>
    <!-- 功能开通弹框 -->
    <el-dialog title="功能开通" :visible.sync="functionMsg.dialogVisible" width="20%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{functionMsg.name}}</div>
        <div style="margin-bottom: 10px;display: flex;" v-for="(item, index) in functionMsg.msg" :key="index">
          <div style="width: 100px;text-align: left;">{{item.name}}：</div>
          <div>
            <el-radio v-model="item.radio" label="1">开通</el-radio>
            </el-radio>
            <el-radio v-model="item.radio" label="2">关闭</el-radio>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="functionMsg.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="functionMsg.dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 服务费管理弹框 -->
    <el-dialog title="服务费管理" :visible.sync="serviceChargeMsg.dialogVisible" width="80%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">商户名称：{{serviceChargeMsg.name}}</div>
        <div style="width: 100%;text-align: left;margin-bottom: 10px;">收款账号：{{serviceChargeMsg.accountNumber}}</div>
        <el-table border class="new-table-fixed-right new-table-fixed" ref="multipleTable" :data="tableData"
          tooltip-effect="dark" :row-style="{'height':'10px'}" :cell-style="{'padding':'5px 0px'}"
          :header-cell-style="headerStyle">
          <el-table-column prop="index" label="序号" width="80">
          </el-table-column>
          <el-table-column v-for="(item, index) in serviceChargeMsg.msg" :key="index" :label="item.name">
          </el-table-column>
          <el-table-column label="平台服务费">
            <div>每笔交易额
              <el-input style="width: 150px;" size="mini" placeholder="请输入百分比交易额"></el-input>%
            </div>
          </el-table-column>
          <el-table-column label="设备服务费">
            <div>每笔交易额
              <el-input style="width: 150px;" size="mini" placeholder="请输入百分比交易额"></el-input>%
            </div>
          </el-table-column>
          <el-table-column label="渠道服务费">
            <div>渠道商名称：xxxxxxxxxxxx</div>
            <div>每笔交易额
              <el-input style="width: 150px;" size="mini" placeholder="请输入百分比交易额"></el-input>%
            </div>
          </el-table-column>
        </el-table>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="serviceChargeMsg.dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="serviceChargeMsg.dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 商户信息弹框 -->
    <el-dialog title="商户信息" :visible.sync="merchantInformation.dialogVisible" width="20%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户名称：xxxxxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户简称：xxxxxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">统一社会信用代码：xxxxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">所属行业：xxxxxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">每周营业日：xxxxxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">营业时间段：xxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">所在区域：xxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">详细地址：xxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户登录名(手机号)：xxxxxxxxx</div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">银联子商户号：xxxxxxxxx</div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="merchantInformation.dialogVisible = false">关 闭</el-button>
      </span>
    </el-dialog>

    <!-- 加盟网点弹框 -->
    <el-dialog title="加盟网点" :visible.sync="franchiseOutlets.dialogVisible" width="30%" :before-close="handleClose">
      <div>
        <div style="width: 100%;text-align: left;margin:0 0 10px 10px;">商户名称：xxxxxxxxxxxx</div>
        <el-tree :data="data" @check="handleNodeClick" show-checkbox node-key="id" :default-expanded-keys="[2, 3]"
          :default-checked-keys="[5]">
        </el-tree>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="franchiseOutlets.dialogVisible = false">关 闭</el-button>
        <el-button type="primary" @click="franchiseOutlets.dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
   
  </div>

</template>

<script>
  import { regionData, CodeToText } from "element-china-area-data";
  export default {
    data() {
      return {
        regionData: regionData,
        selectedOptions: [],
        options: [
          { value: '选项1', label: '黄金糕' },
          { value: '选项2', label: '双皮奶' },
          { value: '选项3', label: '蚵仔煎' },
          { value: '选项4', label: '龙须面' },
          { value: '选项5', label: '北京烤鸭' }
        ],
        ruleForm: {
          name: '',
          startTime: '',
          endTime: '',
          bms_address: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        rules: {
          name: [
            { required: true, message: '请输入活动名称', trigger: 'blur' },
            { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
          ],
          region: [
            { required: true, message: '请选择活动区域', trigger: 'change' }
          ],
          date1: [
            { type: 'date', required: true, message: '请选择日期', trigger: 'change' }
          ],
          date2: [
            { type: 'date', required: true, message: '请选择时间', trigger: 'change' }
          ],
          type: [
            { type: 'array', required: true, message: '请至少选择一个活动性质', trigger: 'change' }
          ],
          resource: [
            { required: true, message: '请选择活动资源', trigger: 'change' }
          ],
          desc: [
            { required: true, message: '请填写活动形式', trigger: 'blur' }
          ]
        },
        data: [{
          id: 1,
          label: '一级 2',
          children: [{
            id: 3,
            label: '二级 2-1',
            children: [{
              id: 4,
              label: '三级 3-1-1'
            }, {
              id: 5,
              label: '三级 3-1-2',
            }]
          }, {
            id: 2,
            label: '二级 2-2',
            children: [{
              id: 6,
              label: '三级 3-2-1'
            }, {
              id: 7,
              label: '三级 3-2-2',
            }]
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'label'
        },
        show3: false,
        checkAll: false,
        checkedCities: ['上海', '北京'],
        isIndeterminate: true,
        // 表格菜单
        tableMsg: [
          { key: 'date', name: '序号' },
          { key: 'date', name: '商户名称' },
          { key: 'date', name: '所属行业' },
          { key: 'date', name: '满减券推广' },
          { key: 'date', name: '抽奖优惠券' },
          { key: 'date', name: '商品推广' },
          { key: 'date', name: '代金券推广' }
        ],
        // 功能管理
        functionMsg: {
          name: "xxxxx",
          dialogVisible: false,
          msg: [
            { radio: '1', name: '满减券推广' },
            { radio: '1', name: '抽奖优惠券' },
            { radio: '1', name: '商品推广' },
            { radio: '1', name: '代金券推广' }
          ]
        },
        // 服务费管理
        serviceChargeMsg: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          msg: [
            { name: '设备', key: 'date' },
            { name: '网点内设备编号', key: 'date' },
          ]
        },
        // 商户信息
        merchantInformation: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          msg: [
            { value: '选项1', label: '黄金糕' },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },
        // 加盟网点
        franchiseOutlets: {
          name: "xxxxxx",
          dialogVisible: false,
          msg: [
            { value: '中国银行长春分行', label: [{ value: '中国银行长春分行', label: [] }] },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },
        // 新增商户
        newlyAdded: {
          name: "xxxxxx",
          accountNumber: "xxxxxx",
          dialogVisible: false,
          value: "",
          value1: [],
          options: [
            { value: '选项1', label: '黄金糕' },
            { value: '选项2', label: '双皮奶' },
            { value: '选项3', label: '蚵仔煎' },
            { value: '选项4', label: '龙须面' },
            { value: '选项5', label: '北京烤鸭' }
          ],
        },
        StateManagement: {
          //单选框选项
          radio: '1',
          //弹出层
          dialogVisible: false,
          dialogVisible1: false,
        },
        //表格头样式
        headerStyle: { 'background': '#4B86BC', 'color': '#ffffff', 'borderRight': '1px solid #E3E5EB', 'padding': '0px', 'height': '45px', 'line-height': '45px' },
        //表格数据
        tableData: [{
          index:1,
          date: '2016-05-02',
          name: '王小虎',
          province: '上海',
          city: '普陀区',
          address: '上海市普陀区金沙江路 1518 弄',
          zip: 200333
        }]
      }
    },
    created() {

    },
    methods: {
      handleNodeClick(leafOnly, includeHalfChecked) {
        console.log(leafOnly, includeHalfChecked);
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length;
        this.checkAll = checkedCount === this.cities.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
      },
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => { });
      },
      handleChange(value) {
        console.log(value);
      },
      /**------分页-------start----*/
      handleSizeChange(val) {
        // console.log(`每页 ${val} 条`);
        this.queryParam.pageSize = val;
        this.queryValveCurrentData();
      },
      handleCurrentChange(val) {
        // console.log(`当前页: ${val}`);
        this.queryParam.currentPage = val;
        this.queryValveCurrentData();
      },
    },
  }
</script>
<style scoped>
  .transition-box {
    width: 200px;
    height: 100px;
    border-radius: 4px;
    background-color: #409EFF;
    text-align: center;
    color: #fff;
    padding: 40px 20px;
    box-sizing: border-box;
    margin-left: 20px;
  }

  .box {
    padding-right: 10px;
    width: 1500px;
    height: 500px;
    overflow-y: scroll;
    overflow-x: hidden;
    table-layout: fixed;
    word-wrap: break-word;
    word-break: break-all;
  }

  .test-5::-webkit-scrollbar {
    /*滚动条整体样式*/
    width: 5px;
    /*高宽分别对应横竖滚动条的尺寸*/
    height: 1px;
  }

  .test-5::-webkit-scrollbar-thumb {
    /*滚动条里面小方块*/
    border-radius: 10px;
    background-color: skyblue;
    background-image: -webkit-linear-gradient(45deg,
        rgba(255, 255, 255, 0.2) 25%,
        transparent 25%,
        transparent 50%,
        rgba(255, 255, 255, 0.2) 50%,
        rgba(255, 255, 255, 0.2) 75%,
        transparent 75%,
        transparent);
  }

  .test-5::-webkit-scrollbar-track {
    /*滚动条里面轨道*/
    box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
    /* background   : ; */
    border-radius: 10px;
  }
</style>


